import React, { useEffect, useState } from 'react'
import './Home.css'
import axios from "axios"
import { ReactSearchAutocomplete } from 'react-search-autocomplete'


const sugerenciaURL = "https://validacion.hgtsa.com.ar/lappa/localidades/sugerenciaget?localidad="
const options = {
    withCredentials: true,
    headers: {
        "Access-Control-Allow-Origin": "*",
        "Content-Type": "application/json",
    }
}



const Home = ({ theme }) => {
    const [sugerenciasOrigen, setSugerenciasOrigen] = useState([])
    const [sugerenciasDestino, setSugerenciasDestino] = useState([])
    const [origen, setOrigen] = useState("")
    const [destino, setDestino] = useState("")
    const handleOnSearchOrigen = (string, results) => {
        // onSearch will have as the first callback parameter
        // the string searched and for the second the results.
        setOrigen(string)
    }
    const handleOnSearchDestino = (string, results) => {
        // onSearch will have as the first callback parameter
        // the string searched and for the second the results.
        setDestino(string)
    }
    const handleOnHoverOrigen = (result) => {
        // the item hovered
    }
    const handleOnHoverDestino = (result) => {
        // the item hovered
    }
    const handleOnSelectOrigen = (item) => {
        // the item selected
        setOrigen(item.name)
    }
    const handleOnSelectDestino = (item) => {
        // the item selected
        setDestino(item.name)
    }
    const handleOnFocusOrigen = () => {
    }
    const handleOnFocusDestino = () => {
    }
    useEffect(() => {
        async function fetchGetSugerencias() {
            await axios.get(sugerenciaURL + origen, { options }).then(res => {
                let tmp = []
                res.data.map((item, index) => {
                    tmp.push({ id: index, name: item.name })
                    return item
                })
                setSugerenciasOrigen(tmp)
            });
            await axios.get(sugerenciaURL + destino, { options }).then(res => {
                let tmp = []
                res.data.map((item, index) => {
                    tmp.push({ id: index, name: item.name })
                    return item
                })
                setSugerenciasDestino(tmp)
            });
        }
        fetchGetSugerencias()
    }, [origen,destino])
    return (
        <div className='homeContainer'>
            <div style={{ width: "40vw" }}>
                <ReactSearchAutocomplete placeholder="Ciudad de origen"
                    items={sugerenciasOrigen}
                    onSearch={handleOnSearchOrigen}
                    onHover={handleOnHoverOrigen}
                    onSelect={handleOnSelectOrigen}
                    onFocus={handleOnFocusOrigen}
                    autoFocus
                />
            </div>
            <div style={{ width: "40vw" }}>
                <ReactSearchAutocomplete placeholder="Ciudad de destino"
                    items={sugerenciasDestino}
                    onSearch={handleOnSearchDestino}
                    onHover={handleOnHoverDestino}
                    onSelect={handleOnSelectDestino}
                    onFocus={handleOnFocusDestino}
                    autoFocus
                />
            </div>
        </div>)
}

export default Home;
